.scheme-of-work {
    padding-bottom: 100px;
}

.scheme-of-work__item {
    padding: 30px;
    background-color: #043c77;
    color: #fff;
    width: 353px;
    height: auto;
}

.scheme-of-work__inner {
    padding-top: 48px;
    display: flex;
    justify-content: space-between;
}

.scheme-of-work__item-top {
    padding-bottom: 30px;
    display: flex;
    justify-content: space-between;
}

.scheme-of-work__item-top a {
    display: grid;
}

.scheme-of-work__item h2 {
    font-family: Montserrat-Medium;
    font-size: 21px;
    line-height: 26px;
}

.scheme-of-work__item p {
    padding-top: 24px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
}


@media (max-width: 320px){
    .scheme-of-work__inner{
        padding-top: 20px;
        display: inline;

    }
    .scheme-of-work__item{
        margin-bottom: 5px;
        padding: 15px;
        background-color: #043c77;
        color: #fff;
        width: auto;
        height: auto;
    }

}